import Article, { Paragraph, Picture } from "../components/Article"

export default function AdministratorsDashboard() {
  return (
    <Article image="providers/dashboard/cover.png">
      <Paragraph
        title="Un dashboard para dominarlo todo"
        paragraph="El dashboard es la pantalla principal de la aplicación, en él podrás acceder a cada rincón de la aplicación."
      />

      <Paragraph
        title="Conoce los widgets"
        paragraph="El dashboard para los usuarios de tipo prestador disponen de los siguientes widgets: A continuación, te ayuda a conocer si un evento está proximo, está por comenzar o está en curso; Eventos, desde aquí podrás revisar los eventos próximos que habrán; Tu progreso, revisa tu conteo de horas; y Tu perfil, que sirve para ver tu información y realizar ciertas cosas."
      />

      <Picture /*url={"providers/dashboard/1.png"}*/ />

      <Paragraph paragraph="Una nota muy importante es de que el widget de Eventos cambia en función de si hay eventos disponibles o no: si no hay eventos disponibles, el widget es más pequeño y solo muestra el icono, en caso contrarío este se torna más grande y muestra la información del evento disponible más próximo." />
    </Article>
  )
}
